<template>
  <div>
    <div>
      <label for="textInput" class="text-gray-900">{{ label }}</label>
    </div>
    <div>
      <input
        type="text"
        class="py-2 px-2 rounded-sm text-gray-700"
        :placeholder="placeHolder"
        @keypress="passToParent"
        v-model="inputValue"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "inputFormComponent",
  props: {
    label: String,
    placeHolder: String
  },
  data: () => {
    return {
      inputValue: ""
    };
  },
  methods: {
    passToParent() {
      console.log("pressed");
      this.$emit("input", this.inputValue);
    }
  }
};
</script>

