// @ts-nocheck
// @ts-ignore

import api from "../../api/api";
const user = JSON.parse(window.localStorage.getItem("user"));
console.log(user);
import router from "../router/index";
const state = {
  me: JSON.parse(window.localStorage.getItem("user")) || null,
  userData: null,
  status: false
};
3;

const getters = {
  getErrorStatus() {
    console.log("get error Status execurted");
    return state.status;
  }
};
const actions = {
  getMe({ commit }) {
    api
      .get("auth/me", {
        params: {},
        headers: {
          Authorization: `Bearer ${
            JSON.parse(window.localStorage.getItem("user")).token
          }`
        }
      })
      .then(res => {
        commit("SET_USER_DATA", res.data);
      })
      .catch(err => {
        console.log(err);
      });
  },
  // @ts-ignore
  login({ commit }, loginData) {
    api
      .post("/auth/login", loginData)
      .then(result => {
        commit("SET_LOGIN_DATA", result.data);
        // @ts-ignore
        window.localStorage.setItem("user", JSON.stringify(result.data));
        commit("SET_ERROR_STATUS", null);

        router.push(`/${result.data.role}`);
      })
      .catch(error => {
        console.error(error);
        commit("SET_ERROR_STATUS", error.response.data);
        // commit("SET_ERROR_STATUS", ...error);
      });
  },
  // ! registration
  register({ commit }, registerData) {
    api
      .post("/auth/register", registerData)
      .then(result => {
        commit("SET_LOGIN_DATA", result.data);
        window.localStorage.setItem("user", JSON.stringify(result.data));
        commit("SET_ERROR_STATUS", null);

        router.push(`/${result.data.role}`);
      })
      .catch(error => {
        console.log(error);
        commit("SET_ERROR_STATUS", error.response.data);
      });
  }
};

const mutations = {
  ["SET_LOGIN_DATA"](state, data) {
    state.me = data;
  },
  ["SET_USER_DATA"](state, data) {
    console.log(data);
    state.userData = data;
  },
  ["SET_ERROR_STATUS"](state, errorResult) {
    console.log("SET ERROR STTATUS EXECUTED");
    state.status = errorResult;
  }
};
export default {
  state,
  getters,
  actions,
  mutations
};
