// @ts-nocheck
import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";
import AdminView from "../views/AdminView.vue";
import ClubView from "../views/ClubView.vue";
import JudgeView from "../views/JudgeView.vue";
import PlayerView from "../views/PlayerView.vue";
import PaymentView from "../views/PaymentView.vue";
import AuthView from "../views/AuthView.vue";
import Me from "../views/Me.vue";
import store from "../store/index";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: HomeView
  },
  {
    path: "/admin",
    name: "admin",
    component: AdminView,
    meta: { requireAuth: true, requireRole: ["admin"] }
  },
  {
    path: "/club",
    name: "club",
    component: ClubView,
    meta: { requireAuth: true, requireRole: ["club"] }
  },
  {
    path: "/judge",
    name: "judge",
    component: JudgeView,
    meta: { requireAuth: true, requireRole: ["judge"] }
  },
  {
    path: "/player",
    name: "player",
    component: PlayerView,
    meta: { requireAuth: true, requireRole: ["player"] }
  },
  {
    path: "/auth",
    name: "auth",
    component: AuthView
  },
  {
    path: "/payment",
    name: "payment",
    component: PaymentView
  },
  {
    path: "/about",
    name: "About"
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
  },
  {
    path: "/me",
    name: "me",
    component: Me
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  console.log(to.meta);
  const { requireAuth, requireRole } = to.meta;
  if (requireAuth && requireRole) {
    if (!store.state.auth.me || null) {
      console.log("authcheck exec");
      next({ name: "auth" });
    }
  }
  if (requireRole) {
    if (store.state.auth.me.role != requireRole[0]) {
      console.log("role missmtached fom index router ");
      next({ name: "auth" });
    }
  }
  next();
});

export default router;
